/* [Master Stylesheet v1.0] */
/* :: :: 1.0 Import Fonts */
@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700,800,900");
/* :: 2.0 Import All CSS */
@import url("/build/css/bootstrap.min.css");
@import url("/build/css/owl.carousel.min.css");
@import url("/build/css/animate.css");
@import url("/build/css/magnific-popup.css");
@import url("/build/css/font-awesome.min.css");
@import url("/build/css/custom-icon.css");
@import url("/build/css/classy-nav.min.css");
/* :: 3.0 Base CSS */
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #fdb700;
  line-height: 1.3;
  font-weight: 700;
}

p {
  color: #a7895a;
  font-size: 14px;
  line-height: 2;
  font-weight: 500;
}

a,
a:hover,
a:focus {
  -webkit-transition-duration: 500ms;
  transition-duration: 500ms;
  text-decoration: none;
  outline: 0 solid transparent;
  color: #141414;
  font-weight: 600;
  font-size: 14px;
}

ul,
ol {
  margin: 0;
}
ul li,
ol li {
  list-style: none;
}

img {
  height: auto;
  max-width: 100%;
}

/* Spacing */
.mt-15 {
  margin-top: 15px !important;
}

.mt-30 {
  margin-top: 30px !important;
}

.mt-50 {
  margin-top: 50px !important;
}

.mt-70 {
  margin-top: 70px !important;
}

.mt-100 {
  margin-top: 100px !important;
}

.mb-15 {
  margin-bottom: 15px !important;
}

.mb-30 {
  margin-bottom: 30px !important;
}

.mb-50 {
  margin-bottom: 50px !important;
}

.mb-70 {
  margin-bottom: 70px !important;
}

.mb-100 {
  margin-bottom: 100px !important;
}

.ml-15 {
  margin-left: 15px !important;
}

.ml-30 {
  margin-left: 30px !important;
}

.ml-50 {
  margin-left: 50px !important;
}

.mr-15 {
  margin-right: 15px !important;
}

.mr-30 {
  margin-right: 30px !important;
}

.mr-50 {
  margin-right: 50px !important;
}

/* Height */
.height-400 {
  height: 400px !important;
}

.height-500 {
  height: 500px !important;
}

.height-600 {
  height: 600px !important;
}

.height-700 {
  height: 700px !important;
}

.height-800 {
  height: 800px !important;
}

/* Section Padding */
.section-padding-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.section-padding-100-0 {
  padding-top: 100px;
  padding-bottom: 0;
}

.section-padding-0-100 {
  padding-top: 0;
  padding-bottom: 100px;
}

.section-padding-100-70 {
  padding-top: 100px;
  padding-bottom: 70px;
}

/* Section Heading */
.section-heading {
  position: relative;
  z-index: 1;
  max-width: 600px;
  margin-bottom: 80px !important;
}
.section-heading span {
  color: #a7895a;
  font-size: 12px;
  text-transform: uppercase;
  margin-bottom: 15px;
  display: block;
  letter-spacing: 2px;
}
.section-heading h3 {
  font-size: 34px;
  margin-bottom: 0;
  font-weight: 600;
}
@media only screen and (max-width:767px) {
  .section-heading h3 {
    font-size: 24px;
  }
}
.section-heading.text-left {
  text-align: left !important;
}
.section-heading.white span,
.section-heading.white h3 {
  color: #ffffff;
}

/* Preloader */
#preloader {
  background: -webkit-linear-gradient(to right bottom, #00ccff, #12bbf5, #24aae9, #3399dc, #3f88cd, #3f88cd, #3f88cd, #3f88cd, #3399dc, #24aae9, #12bbf5, #00ccff);
  background: linear-gradient(to right bottom, #00ccff, #12bbf5, #24aae9, #3399dc, #3f88cd, #3f88cd, #3f88cd, #3f88cd, #3399dc, #24aae9, #12bbf5, #00ccff);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5000;
}
#preloader .circle-preloader {
  display: block;
  width: 60px;
  height: 60px;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-bottom-color: #ffffff;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  animation: spin 2s infinite linear;
}
@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

/* Miscellaneous */
.bg-img {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.bg-white {
  background-color: #ffffff !important;
}

.bg-dark {
  background-color: #000000 !important;
}

.bg-transparent {
  background-color: transparent !important;
}

.font-bold {
  font-weight: 700;
}

.font-light {
  font-weight: 300;
}

.bg-overlay {
  position: relative;
  z-index: 2;
  background-position: center center;
  background-size: cover;
}
.bg-overlay::after {
  background-color: rgba(0, 0, 0, 0.88);
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
}

.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  color: #ffffff;
  right: 0;
  padding-right: 0;
  width: 30px;
  height: 30px;
  background-color: #ffff;
  line-height: 30px;
  text-align: center;
  position: absolute;
  top: 40px;
  right: -30px;
}
@media only screen and (max-width:767px) {
  .mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
    right: 0;
  }
}

/* ScrollUp */
#scrollUp {
  background: -webkit-linear-gradient(to right bottom, #00ccff, #12bbf5, #24aae9, #3399dc, #3f88cd, #3f88cd, #3f88cd, #3f88cd, #3399dc, #24aae9, #12bbf5, #00ccff);
  background: linear-gradient(to right bottom, #00ccff, #12bbf5, #24aae9, #3399dc, #3f88cd, #3f88cd, #3f88cd, #3f88cd, #3399dc, #24aae9, #12bbf5, #00ccff);
  border-radius: 0;
  bottom: 60px;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.3);
  color: #ffffff;
  font-size: 24px;
  height: 40px;
  line-height: 40px;
  right: 60px;
  text-align: center;
  width: 40px;
  -webkit-transition-duration: 500ms;
  transition-duration: 500ms;
}
#scrollUp:hover {
  background-color: #141414;
}
@media only screen and (max-width:767px) {
  #scrollUp {
    bottom: 30px;
    right: 30px;
  }
}

/* Essence Button */
