/* Testimonials Area CSS */
.single-testimonial-area {
  position: relative;
  z-index: 1;
}
.single-testimonial-area .testimonial-thumb {
  -webkit-transition-duration: 500ms;
  transition-duration: 500ms;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 55px;
  flex: 0 0 55px;
  max-width: 55px;
  width: 55px;
  height: 55px;
